<template>
  <transition name="modal" appear appear-active-class="modal-enter-active">
    <div
      ref="modal"
      class="modal modal-backdrop"
      role="dialog"
      aria-describedby="modal-description"
      aria-labelledby="modal-title"
      tabindex="0"
      @click="close"
      @keydown.esc="close"
    >
      <div class="modal-wrapper">
        <div class="modal-container" :class="modalWidth" @click.stop>
          <i v-if="isDismissible" class="fa fa-times modal-close" @click="close"></i>

          <header v-if="title" id="modal-title" class="modal__header">
            <div class="row">
              <div class="col-12">
                <h1>{{ title }}</h1>
              </div>
            </div>
          </header>

          <section id="modal-description" class="modal__body">
            <p v-if="description">{{ description }}</p>

            <slot v-else name="description"></slot>
          </section>

          <footer
            v-if="cancelAction || confirmAction"
            class="modal__footer"
            :class="{ 'flex-end': !cancelAction && confirmAction }"
          >
            <button v-if="cancelAction" type="button" class="modal__btn-cancel btn btn-secondary" @click="cancelAction">
              {{ cancelText }}
            </button>

            <button
              v-if="confirmAction"
              type="button"
              class="modal__btn-confirm btn btn-primary"
              @click="confirmAction"
            >
              {{ confirmText }}
            </button>
          </footer>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
const VALID_MODAL_SIZES = ['sm', 'md', 'lg', 'full'];

export default {
  name: 'SjModal',

  props: {
    isDismissible: {
      default: true,
      type: Boolean,
    },

    cancelAction: {
      default: undefined,
      type: Function,
    },

    cancelText: {
      default: 'Cancel',
      type: String,
    },

    confirmAction: {
      default: undefined,
      type: Function,
    },

    confirmText: {
      default: 'Save',
      type: String,
    },

    description: {
      default: undefined,
      type: String,
    },

    size: {
      default: 'md',
      type: String,
      validator: (size) => {
        return VALID_MODAL_SIZES.indexOf(size) > -1;
      },
    },

    title: {
      default: undefined,
      type: String,
    },
  },

  data() {
    const sizeMap = {
      sm: 'modal-sm',
      md: 'modal-md',
      lg: 'modal-lg',
      full: 'modal-full',
    };

    return {
      modalWidth: sizeMap[this.size],
    };
  },

  mounted() {
    this.$refs.modal.focus();
  },

  methods: {
    close() {
      this.$emit('close');
    },
  },
};
</script>

<style scoped lang="scss">
$mobile-screen: 576px;

.modal {
  &__footer {
    display: flex;
    flex-wrap: wrap-reverse;
    justify-content: space-between;
    align-items: baseline;
    margin-top: 10px;

    &.flex-end {
      justify-content: flex-end;
    }
  }

  &-backdrop {
    background-color: rgba(0, 0, 0, 0.5);
    display: table;
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    transition: opacity 170ms ease;
    width: 100%;
    z-index: 9998;

    .modal-wrapper {
      display: table-cell;
      vertical-align: middle;

      .modal-container {
        background-color: #fff;
        border-radius: 2px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
        margin: 0 auto;
        max-height: calc(100vh - 50px);
        overflow-y: scroll;
        padding: 24px;
        position: relative;
        transition: all 170ms ease;

        .modal-close {
          color: blue;
          cursor: pointer;
          position: absolute;
          right: 10px;
          top: 10px;
        }
      }
    }
  }

  &-full {
    width: 98%;
  }

  &-lg {
    width: 80%;
  }

  &-md {
    width: 60%;
  }

  &-sm {
    width: 30%;
  }
}

// Transition styles
.modal-enter-active,
.modal-leave-active {
  opacity: 0;

  .modal-container {
    transform: scale(1.1);
  }
}
</style>
