<template>
  <div>
    <label :for="inputId" class="text-field">
      <p class="text-field__title">{{ inputLabel }}</p>
      <input
        class="text-field_input"
        :type="type"
        :class="{ 'text-field_input_error': inputError, 'text-field_input_success': inputSuccess }"
        :required="required"
        :name="name"
        :maxlength="maxLegnth"
        :id="inputId"
        @blur="validate"
        v-model="inputValue"
        :placeholder="placeholder"
      />
      <div v-if="inputError" class="text-field__error-text"><strong>Error:</strong> {{ inputError }}</div>
    </label>
  </div>
</template>

<script>
export default {
  data() {
    return {
      inputError: false,
      inputSuccess: false,
    };
  },
  name: 'textField',
  props: {
    required: {
      type: Boolean,
      default: false,
    },
    name: {
      type: String,
      default: null,
    },
    maxLegnth: {
      type: Number,
      default: null,
    },
    placeholder: {
      type: String,
      default: null,
    },
    inputId: {
      type: String,
      default: 'input-id',
    },
    inputLabel: {
      type: String,
      default: 'Label title',
    },
    inputValue: {
      type: String,
      default: null,
    },
    type: {
      type: String,
      default: 'email',
    },
  },
  methods: {
    validate() {
      const inputValue = this.inputValue;
      const inputLength = inputValue.length > 0;

      if (inputLength && this.type === 'email') {
        this.fenceEmail(inputValue);
      } else if (inputLength && this.type === 'password') {
        this.fencePassword(inputValue);
      } else if (!inputLength && this.required) {
        this.inputError = `The ${this.type} field is requred`;
        this.inputSuccess = false;
      } else if (!inputLength) {
        this.inputSuccess = false;
        this.inputError = false;
      }
    },
    fenceEmail(inputText) {
      if (inputText.includes('@')) {
        this.inputError = false;
        this.inputSuccess = true;
      } else {
        this.inputError = 'Please enter a valid email address';
        this.inputSuccess = false;
      }
    },
    fencePassword(inputText) {
      const symbols = ['!', '@', '#', '$', '%', '^', '&', '*'];
      let validPassword = symbols.some((symbol) => inputText.includes(symbol));
      if (validPassword) {
        this.inputError = false;
        this.inputSuccess = true;
      } else {
        this.inputError = 'Please enter a valid password';
        this.inputSuccess = false;
      }
    },
  },
};
</script>

