  display: flex;
  flex-direction: column;
  margin: 10px 0 10px 0;
  &__title {
    text-align: left;
    margin: 0px;
    padding-bottom: 5px;
  }
  &__error-text {
    text-align: left;
    margin-top: 5px;
  }
}
.text-field_input {
  max-width: 250px;
  font-size: 12pt;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid rgba(128, 128, 128, 0.378);
  &::placeholder {
    color: rgba(128, 128, 128, 0.678);
  }
}
.text-field_input_error {
  border: 1px solid red;
}
.text-field_input_success {
  border: 1px solid lime;
}
</style>
